.slick-slide {
    position: relative;
}
.slick-slide > div > div {
    position: relative;
}
.slick-slide > div > div::before {
    content: '';
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: rgba(97, 218, 251, .3);
}
.slick-prev, .slick-next {
    z-index: 1;
    width: auto;
    height: auto;
}
.slick-prev:before, .slick-next:before {
    font-size: 32px;
}
.homeBanner .slick-slide img {
    height: 90vh;
    width: 100%;
    object-fit: cover;
    object-position: center center;
    pointer-events: none;
}
.slick-slide .caption {
    color: #f9f9f9;
}
.caption * {
    text-shadow: -1px 1px 3px rgb(11 11 11);
}
.caption h2 {
    text-transform: uppercase;
    font-weight: 500;
}
.slick-dots {
    z-index: 1;
    bottom: 0;
}
.slick-dots path {
    filter: drop-shadow(0px 0px 60px #0b0b0b);
    border-radius: 50%;
    opacity: 0.5;
}
.slick-dots .slick-active path {
    fill: #f9f9f9;
    opacity: 0.9;
}
.slick-dots li button:before, .slick-dots li.slick-active button:before {
    color: #f9f9f9;
}

/*====================================================================Start Responsive====================================================================*/
@media only screen and (max-width: 576px) {
    .homeBanner .slick-slider {
        height: 100vh;
    }
}
@media only screen and (min-width: 320px) {
    .slick-prev {
        left: 1%;
    }
    .slick-next {
        right: 1%;
    }
    .slick-slide .caption {
        position: absolute;
        bottom: 20px;
        width: 100%;
    }
}
/*====================================================================End Responsive====================================================================*/