@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

/* width */
::-webkit-scrollbar {
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #272b36;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 15px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.App {
  text-align: center;
}
.App-header {
  color: white;
}
body {
    overflow-x: hidden;
}
.bodyparallax {
    width: 100%;
    position: fixed;
    z-index: -1;
    opacity: 0.5;
}
.bodyparallax img {
    width: 100%;
    object-position: 74%;
    height: 100vmax;
    filter: invert(70%) brightness(200%) opacity(0.7);
}
html.modal-open {
    overflow: hidden;
}
