h1, h2 {
    margin: 1em 0;
    text-shadow: 1px 0px 2px #a9a9a9;
    clear: both;
}
h1 > span, h2 > span {
    color: #008b8b;
    filter: hue-rotate(45deg);
}
p {
    padding-bottom: 0.7em;
}
