/*--------------------------------------------Start Products Categories--------------------------------------------*/
.categories-list > li > a, .sub-categories > li > a, .products > li > a {
    display: block;
    border: 3px solid #a4a4a4;
}
/*--------------------------------------------End Products Categories--------------------------------------------*/
#main {
    min-height: calc(100vh - 82px);
    padding-top: 124px;
}
.container {
    padding: 0 15px;
    margin: 0 auto;
}
.skeleton-loading > .skeleton-row > .skeleton-circle, .skeleton-loading > .skeleton-row > .skeleton-text {
    position: relative;
    background-color: #f7f7f7;
    border-radius: 0.25em;
}
.skeleton-loading > .skeleton-row > .skeleton-circle {
    min-height: 5em;
}
.skeleton-loading > .skeleton-row > .skeleton-text {
    min-height: 2.5em;
}
.skeleton-row > div::before {
    animation: skeleton-loading infinite 1s linear alternate;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 0.25em;
}
